  </div>
</template>
<script>
import BookCard from '@/components/Book/BookCard.vue'
import bookService from '@/api/bookServices'
import { watchEffect } from 'vue'
import { useCookies } from "vue3-cookies";

export default {
  name: 'Book',
  // components: { BookCard },
  setup() {
    const { cookies } = useCookies();
    return { cookies };
  },

  watch: {
    // whenever question changes, this function will run
    deferredPrompt(newdeferredPrompt, olddeferredPrompt) {
      console.log('OLD')
      console.log(olddeferredPrompt)
      console.log('------------------')

      console.log('NEW')
      console.log(newdeferredPrompt)
      console.log('------------------')

      if (newdeferredPrompt != null) {
        this.installApp();
      }
    }
  },

  created() {
    watchEffect(() => {
      this.books = null
      this.getBooks(this.page);
    })

    window.addEventListener('beforeinstallprompt', function (e) {
      console.log('testBEFORE')
      // Prevent Chrome 67 and earlier from automatically showing the prompt
      e.preventDefault();
      // Stash the event so it can be triggered later.
      // if (!this.cookies.get("add-to-home-screen")) {
      this.deferredPrompt = e;
      console.log('after beforInstall')
      console.log(this.deferredPrompt)


      //      }
    });
    window.addEventListener("appinstalled", () => {
      this.deferredPrompt = null;
    });

  },
  props: ['page'],
  data() {
    return {
      books: [],
      openBook: [],
      totalBooks: 0,
      current: 1,
      search: "",
      empty: "",
      deferredPrompt: null

    }
  },
  methods: {

    installApp() {

      const swalWithBootstrapButtons = this.$swal.mixin({
        customClass: {
          confirmButton: 'btn btn-primary btn-lg',
          cancelButton: 'btn btn-outline-primary btn-lg ms-2'
        },
        buttonsStyling: false
      })

      swalWithBootstrapButtons.fire({
        title: 'تطبيق مصغر بانتظارك',
        text: `بإمكانك الان تحميل تطبيق (توثيق القراءة مع أصبوحة 180) على هاتفك المحمول لتكون تجربتك في استخدامه دائما سهلة وميسرة`,
        imageUrl: 'https://scontent.fruh4-6.fna.fbcdn.net/v/t1.15752-9/323030753_541563567697095_8306929529036486640_n.png?_nc_cat=111&ccb=1-7&_nc_sid=ae9488&_nc_ohc=SuOeu1pwBPkAX9WAOLj&_nc_ht=scontent.fruh4-6.fna&oh=03_AdShEUDaS_njEg2s8bMZiLDO6WQoNyRJokP_infrZc_1xg&oe=63DA9123',
        showCancelButton: true,
        confirmButtonText: 'تحميل',
        cancelButtonText: 'لاحقًا  ',
        showClass: {
          popup: 'animate__animated animate__zoomIn'
        },
        hideClass: {
          popup: 'animate__animated animate__zoomOut'
        }
      })
        .then((willDelete) => {
          if (willDelete.isConfirmed) {
            this.deferredPrompt.prompt();
          }
          else {
            this.cookies.set("add-to-home-screen", 'later', { expires: 5 });
            this.deferredPrompt = null;
          }
        })
    },

    async getBooks(page) {
      const response = await bookService.getAllBooks(page);
      this.books = response.books.data;
      this.openBook = response.open_book;
      this.totalBooks = response.books.total
      this.current = page
    },

    async filteredBooks() {
      this.empty = ''

      if (this.search) {
        const response = await bookService.getBookByName(this.search);
        if (response == 'empty') {
          this.empty = 'لا يوجد كتاب بهذا الاسم'
        }
        else {
          this.books = response.data;
          this.totalBooks = response.total

        }
      }
      else {
        const response = await bookService.getAllBooks(this.page);
        this.books = response.books.data;
      }
    },
    checkActive(item) {
      let className = ''
      if (this.current == item) {
        className = 'active'
      }
      else {
        className = className + ' done'
      }
      return className
    }

  },
  computed: {
    hasNextPage() {
      var totalPages = Math.ceil(this.totalBooks / 10)
      return this.page < totalPages
    },
    totalPages() {
      return Math.ceil(this.totalBooks / 10)
    },
  },

}

</script>
